<template>
    <div>
        <div class="news-wrap">
            <section class="video">
                <video src="../../assets/images/share/movie.ogg" width="100%;" controls="controls"></video>
            </section>
            <section class="top">
                <h3></h3>
                <ul>
                    <li></li>
                    <li>123321</li>
                    <li>3242</li>
                </ul>
            </section>
            <section class="cont">
                
                <h3>1. </h3>
                <p>8.7%2807503-4.5,5%(1%)18.8%
</p>
            </section>
            <footer @click="zan()"></footer>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {

        }
    },
    methods : {
        zan () {
            alert('')
        }
    }
}
</script>
<style lang="scss" scoped>
    .news-wrap{
        margin-top:80px;
        background:#fff;
        .video video{
            width:100%;
            // height:440px;
        }
        .top{
            padding:25px;
            border-bottom:1px solid #ddd;
            h3{
                font-size: 40px;
                color: #333333;
            }
            ul{
                display: flex;
                flex-flow: nowrap row;
                justify-content: space-between;
                margin-top:18px;
                li{
                    color:#888;
                    flex-grow: 1;
                    font-size: 24px;
                }
                li:nth-of-type(1){
                    color: #2B8CFF;
                }
                li:nth-of-type(2){
                    text-align: center;
                    
                }
                li:nth-of-type(3){
                    text-align: right;
                }
            }

        }
        .cont,.cont p{
            font-size:30px;
            color:#333;
            line-height: 60px;
            padding:20px 25px;
            h3{
                font-size:32px;
            }
        }
        footer{
            position: fixed;
            bottom:0;
            display: block;
            width:100%;
            height: 98px;
            font-size:34px;
            line-height: 98px;
            text-align: center;
            color:#fff;
            background: #2B8CFF;
        }
    }
</style>





